	module.exports = {"data":{"markdownRemark":{"html":"<p><strong>“<a href=\"/!/dst/Hey!.mp3\">Hey!” を聞く</a> (4:27 | [filesize]<a href=\"https://danmaq.com/!/dst/Hey!.mp3%5B/filesize%5D\">https://danmaq.com/!/dst/Hey!.mp3[/filesize]</a>)</strong></p>\n<p>南国のお祭り風にチャレンジ。この頃は別名義で楽曲作っていて、この曲もその一つ。 ボイスは当時付き合ってた女の子に吹き込んでもらったが、緊張しすぎたのか何度収録しても声が上ずってしまいうまく録音できなかったので、無理やり波形弄っていつもの声の高さに修正。丁度フランジャーかかったような感じになってイイかも、って思ったのでそのまま収録。  ちなみに他にもいくつかボイスを収録してもらったのだが、全部こんな感じでひどいボイスになったのでお蔵入り。</p>\n<h2>追記</h2>\n<p>ちなみに私も当時マスタリング知識が全くなく、ノーマライズやコンプレッサー、リミッターなどの知識を得たのは丸一年半後の2005年中頃。音圧が足りない……と頭を捻ってボリュームをマシマシにし続けたところ、こんなノイジーな曲が出来上がりました。ちなみに作業データはこの翌々月に消失していたたため、再マスタリングもできずorz なお、この曲は<a href=\"/3636\">danmaq soundtrack</a> にも収録しています。</p>","frontmatter":{"cover":null,"date":"2004-01-02T23:37:13+09:00","strDate":"2004/1/2","title":"Hey!"}}},"pathContext":{"path":"/ja/blog/2004/01/02-hey/","langKey":"ja"}}

